body {
	font-family: 'DM Sans', sans-serif;
	color: var(--main-black-color);
	font-size: 16px;
	letter-spacing: 0.04em;
	background-color: var(--main-white-color);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
	padding: 0;
	margin: 0;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
}
.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;

	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.container {
	width: 100%;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
	@include mob {
		width: 480px;
	}

	@include tab {
		width: 768px;
	}

	@include desk {
		width: 1200px;
	}
}
a {
	text-decoration: none;
	color: inherit;
	font-weight: inherit;
	font-size: inherit;
	line-height: inherit;
	letter-spacing: inherit;
}
ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}
button {
	padding: 0;
	font-family: inherit;
	cursor: pointer;
	background-color: transparent;
	border: none;
	outline: none;
}
